import React, {Component} from 'react';
import {Table} from 'semantic-ui-react'
import color from '../colors';

class Item extends Component {

    wasModifiedToday = (item,e) => {
        // console.log(item.timestamp);
        return ((new Date()).toDateString() === (new Date(item.timestamp)).toDateString());
    }
   

    render() {
        const props = this.props;
        return (
            <Table.Row style={{backgroundColor: color(props.item.tags)}} 
                onClick={props.rowClickEvent} 
            >
                <Table.Cell width={1}>
                    <strong><i>{(this.wasModifiedToday(props.item)?" ":"")}</i></strong>
                </Table.Cell>
                <Table.Cell width={11}> 
                    {props.item.name} <strong >{props.item.marked ? " *" : ""}</strong>
                </Table.Cell>
                {/* <Table.Cell width={5}> {props.item.tags}</Table.Cell> */}
                {/* <Table.Cell style={{fontFamily:"Roboto Black"}}  textAlign="right" width={1}> {props.item.marked ? "*" : ""}</Table.Cell> */}
                <Table.Cell width={4}>
                    { props.item.qty + " " + props.item.unit}
                </Table.Cell>
            </Table.Row>
        )
    }
}

export default Item;




