import React, { Component } from 'react';
import {TransitionablePortal, Segment, Button, Label, Container  } from 'semantic-ui-react';
import color from '../colors';
import CategoryItems from './CategoryItems'

class CategoryPortal extends Component {

    state = { activeIndex: -1, category: "" }

    handleClick = (e, titleProps) => {
        console.log(titleProps);
        const { index } = titleProps
        const { activeIndex } = this.state
        const newIndex = activeIndex === index ? -1 : index
    
        this.setState({ activeIndex: newIndex, category: titleProps.children })
    }

    stopPropagation = (e) => {
        e.stopPropagation();
      }
  
    render() {  
        const categories = [...new Set(this.props.items.map(item => item.tags))].sort();
        const renderedCategories = categories.map((category,index) => {
            return (
                <span verticalAlign='middle'>
                    <Label onClick={this.props.onSelect.bind(this,category)} style={{width:'50%', height:48, margin:0, backgroundColor:'transparent', color:'white',color:color(category)}}>{category}</Label>
                </span>
            )   
        });  
        return (
            <div>
                <TransitionablePortal open={this.props.open} transition={{animation:'fade left', duration:500}}  closeOnDocumentClick={false}  onClose={this.closeNavigator}>  
                    <div style={{ backgroundColor: 'transparent', left: '0%', padding:0, position: 'fixed', top: '0px', zIndex: 5000, width:"100%", height:"100%", borderRadius:0}}
                        onClick={this.props.onClose}
                    >
                        <Segment raised floated="right" inverted style={{ boxShadow: "0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22)", borderRadius:"0em", right: '0%', margin:0,  position: 'fixed', top: '62px', zIndex: 1000, width:"50%", height:"100%",borderRadius:"0em" }}  textAlign="center" 
                            onClick={this.stopPropagation}
                        >
                            {renderedCategories}
                        </Segment>
                    </div>
                </TransitionablePortal>
            </div>
        );
    }
}

export default CategoryPortal;



