import React, { Component } from 'react';
import {TransitionablePortal, Segment, Button, Label, Container  } from 'semantic-ui-react';

class IndexPortal extends Component {

    state = { selectedLetter: "", startingLetters:[] }

    onOpenAZ = (e) => {
        let startLetters=[];
        this.props.items.forEach((item) => {
          startLetters.push(item.name.charAt(0).toUpperCase());
        });
        let distinctLetters = Array.from(new Set(startLetters));
        console.log(distinctLetters);
        this.setState({startingLetters:distinctLetters});
    }    
    
    stopPropagation = (e) => {
        e.stopPropagation();
    }
      
    render() {  
        return (
            <div>
                <TransitionablePortal open={this.props.open} transition={{animation:'fade left', duration:500}}  closeOnDocumentClick={false}  onClose={this.props.onClose} onOpen={this.onOpenAZ} >  
                <div style={{ backgroundColor: 'transparent', left: '0%', padding:0, position: 'fixed', top: '0px', zIndex: 5000, width:"100%", height:"100%", borderRadius:0}}
                    onClick={this.props.onClose}
                >
                    <Segment basic raised floated="right" inverted  style={{right: '0%', marginRight:0, borderRadius:"0em", position:'fixed', top: '62px', zIndex: 5000, height:"100%", width:"40%"}}  
                        onClick={this.stopPropagation}
                    >
                        <Button.Group widths="3" style={{height:40}} >
                        <Button className="ui black compact" toggle active={this.state.selectedLetter==='A'} disabled={!this.state.startingLetters.includes('A')} onClick={this.props.letterSelected.bind(this, 'A')} style={{borderRadius:"0em"}}>A</Button>
                        <Button className="ui black compact" toggle active={this.state.selectedLetter==='B'} disabled={!this.state.startingLetters.includes('B')} onClick={this.props.letterSelected.bind(this, 'B')} style={{borderRadius:"0em"}}>B</Button>
                        <Button className="ui black compact" toggle active={this.state.selectedLetter==='C'} disabled={!this.state.startingLetters.includes('C')} onClick={this.props.letterSelected.bind(this, 'C')} style={{borderRadius:"0em"}}>C</Button>
                        </Button.Group>
                        <Button.Group widths="3" style={{height:40}}>
                        <Button className="ui black compact" toggle active={this.state.selectedLetter==='D'} disabled={!this.state.startingLetters.includes('D')} onClick={this.props.letterSelected.bind(this, 'D')} style={{borderRadius:"0em"}}>D</Button>
                        <Button className="ui black compact" toggle active={this.state.selectedLetter==='E'} disabled={!this.state.startingLetters.includes('E')} onClick={this.props.letterSelected.bind(this, 'E')} style={{borderRadius:"0em"}}>E</Button>
                        <Button className="ui black compact" toggle active={this.state.selectedLetter==='F'} disabled={!this.state.startingLetters.includes('F')} onClick={this.props.letterSelected.bind(this, 'F')} style={{borderRadius:"0em"}}>F</Button>
                        </Button.Group>
                        <Button.Group widths="3" style={{height:40}}>
                        <Button className="ui black compact" toggle active={this.state.selectedLetter==='G'} disabled={!this.state.startingLetters.includes('G')} onClick={this.props.letterSelected.bind(this, 'G')} style={{borderRadius:"0em"}}>G</Button>
                        <Button className="ui black compact" toggle active={this.state.selectedLetter==='H'} disabled={!this.state.startingLetters.includes('H')} onClick={this.props.letterSelected.bind(this, 'H')} style={{borderRadius:"0em"}}>H</Button>
                        <Button className="ui black compact" toggle active={this.state.selectedLetter==='I'} disabled={!this.state.startingLetters.includes('I')} onClick={this.props.letterSelected.bind(this, 'I')} style={{borderRadius:"0em"}}>I</Button>
                        </Button.Group>
                        <Button.Group widths="3" style={{height:40}}>
                        <Button className="ui black compact" toggle active={this.state.selectedLetter==='J'} disabled={!this.state.startingLetters.includes('J')} onClick={this.props.letterSelected.bind(this, 'J')} style={{borderRadius:"0em"}}>J</Button>
                        <Button className="ui black compact" toggle active={this.state.selectedLetter==='K'} disabled={!this.state.startingLetters.includes('K')} onClick={this.props.letterSelected.bind(this, 'K')} style={{borderRadius:"0em"}}>K</Button>
                        <Button className="ui black compact" toggle active={this.state.selectedLetter==='L'} disabled={!this.state.startingLetters.includes('L')} onClick={this.props.letterSelected.bind(this, 'L')} style={{borderRadius:"0em"}}>L</Button>
                        </Button.Group>
                        <Button.Group widths="3" style={{height:40}}>
                        <Button className="ui black compact" toggle active={this.state.selectedLetter==='M'} disabled={!this.state.startingLetters.includes('M')} onClick={this.props.letterSelected.bind(this, 'M')} style={{borderRadius:"0em"}}>M</Button>
                        <Button className="ui black compact" toggle active={this.state.selectedLetter==='N'} disabled={!this.state.startingLetters.includes('N')} onClick={this.props.letterSelected.bind(this, 'N')} style={{borderRadius:"0em"}}>N</Button>
                        <Button className="ui black compact" toggle active={this.state.selectedLetter==='O'} disabled={!this.state.startingLetters.includes('O')} onClick={this.props.letterSelected.bind(this, 'O')} style={{borderRadius:"0em"}}>O</Button>
                        </Button.Group>
                        <Button.Group widths="3" style={{height:40}}>
                        <Button className="ui black compact" toggle active={this.state.selectedLetter==='P'} disabled={!this.state.startingLetters.includes('P')} onClick={this.props.letterSelected.bind(this, 'P')} style={{borderRadius:"0em"}}>P</Button>
                        <Button className="ui black compact" toggle active={this.state.selectedLetter==='Q'} disabled={!this.state.startingLetters.includes('Q')} onClick={this.props.letterSelected.bind(this, 'Q')} style={{borderRadius:"0em"}}>Q</Button>
                        <Button className="ui black compact" toggle active={this.state.selectedLetter==='R'} disabled={!this.state.startingLetters.includes('R')} onClick={this.props.letterSelected.bind(this, 'R')} style={{borderRadius:"0em"}}>R</Button>
                        </Button.Group>
                        <Button.Group widths="3" style={{height:40}}>
                        <Button className="ui black compact" toggle active={this.state.selectedLetter==='S'} disabled={!this.state.startingLetters.includes('S')} onClick={this.props.letterSelected.bind(this, 'S')} style={{borderRadius:"0em"}}>S</Button>
                        <Button className="ui black compact" toggle active={this.state.selectedLetter==='T'} disabled={!this.state.startingLetters.includes('T')} onClick={this.props.letterSelected.bind(this, 'T')} style={{borderRadius:"0em"}}>T</Button>
                        <Button className="ui black compact" toggle active={this.state.selectedLetter==='U'} disabled={!this.state.startingLetters.includes('U')} onClick={this.props.letterSelected.bind(this, 'U')} style={{borderRadius:"0em"}}>U</Button>
                        </Button.Group>
                        <Button.Group widths="3" style={{height:40}}>
                        <Button className="ui black compact" toggle active={this.state.selectedLetter==='V'} disabled={!this.state.startingLetters.includes('V')} onClick={this.props.letterSelected.bind(this, 'V')} style={{borderRadius:"0em"}}>V</Button>
                        <Button className="ui black compact" toggle active={this.state.selectedLetter==='W'} disabled={!this.state.startingLetters.includes('W')} onClick={this.props.letterSelected.bind(this, 'W')} style={{borderRadius:"0em"}}>W</Button>
                        <Button className="ui black compact" toggle active={this.state.selectedLetter==='X'} disabled={!this.state.startingLetters.includes('X')} onClick={this.props.letterSelected.bind(this, 'X')} style={{borderRadius:"0em"}}>X</Button>
                        </Button.Group>
                        <Button.Group widths="3" style={{height:40}}>
                        <Button className="ui black compact" toggle active={this.state.selectedLetter==='Y'} disabled={!this.state.startingLetters.includes('Y')} onClick={this.props.letterSelected.bind(this, 'Y')} style={{borderRadius:"0em"}}>Y</Button>
                        <Button className="ui black compact" toggle active={this.state.selectedLetter==='Z'} disabled={!this.state.startingLetters.includes('Z')} onClick={this.props.letterSelected.bind(this, 'Z')} style={{borderRadius:"0em"}}>Z</Button>
                        <Button className="ui black compact"/>
                        </Button.Group>
                        {/* <Segment basic textAlign="center" style={{position:'fixed',width:"40%", bottom:0, right:0}}>
                        <Button icon="angle up" size="huge" className="ui black" onClick={this.closeAZ}></Button>
                        </Segment> */}
                    </Segment>
                </div>
                </TransitionablePortal>
            </div>
        );
    }
}

export default IndexPortal;



