import React, { Component } from 'react';
import './App.css';
import {
  Button,
  Dropdown,
  List,
  Icon,
//  Modal,
  Input,
  Divider,
//  Image,
//  Label,
//  Form,
//  Rail,
//  Search,
  Table, 
  Grid,
//  Transition,
  Container,
  Popup,
//  Header,
  Menu,
  Segment,
  TransitionablePortal
} from 'semantic-ui-react';
//import TransitionGroup from 'semantic-ui-react/dist/commonjs/modules/Transition/TransitionGroup';
import Item from "./components/Item";
import Keypad from "./components/Keypad";
import EmailComposer from "./components/EmailComposer";
import Categories from './components/Categories';
import CategoryPortal from './components/CategoryPortal';
import IndexPortal from './components/IndexPortal';

const POSTURL = "https://api.jsonbin.io/b/5a84f744849a8f1c811922ad";
// const FETCHURL = "https://api.jsonbin.io/b/5a7cfa997ecc101273331408";
const FETCHURL = "https://api.jsonbin.io/b/5a84f744849a8f1c811922ad/latest";

var newInput=true;

class App extends Component {

  state = {
    searchText: "",
    items: [{name:"", qty:"", unit:"", tags:""}],
    openKeypad: false,
    marked:false,
    selectedItem:{name:"", qty:0, unit:"", tags:""},
    saved:false,
    changed:false,
    status:"",
    sortMode:"None",
    openEmailComposer:false,
    showRelated: false,
    selectedCategory: "",
    showAddPopup:false,
    showCategoryModal:false,
    showCategoryPortal:false,
    showIndexPortal:false,
    selectedLetter:""
  }


  // Fetch and save **********************

  componentDidMount() {
    fetch(FETCHURL)
        .then(response => response.json())
        .then(data => {
          this.fetchedItems = data.Items;
          // for (let i=0; i<data.Items.length; i++)
          //   delete data.Items[i].timestamp; 
          this.setState({items:data.Items})}
        );
  }

  fetchedItems = {};

  changed = () => JSON.stringify(this.fetchedItems) === JSON.stringify(this.state.items);

  handleSaveSendButtonClick = () => {
    if (this.state.changed) {
      this.sendData();
      return;
    }
    this.setState({openEmailComposer:true})
  }

  closeEmailComposer = () => {
    this.setState({openEmailComposer:false})
  }

  sendData = () => {
    this.setState({status:"saving"})
    this.postData(POSTURL, {Date: (new Date()).toJSON(), Items: this.state.items})
    .then(data => {
      console.log(data);
      if (!data.success)
        throw new Error("Save failed");
      this.fetchedItems = data.data.Items;
      console.log(this.changed());
      this.setState({saved:true, changed:false, status:"saved"})
      setTimeout(() => {
        this.setState({ saved: false, status:"" })
      }, 500)
    }) // JSON from `response.json()` call
    .catch(error => {
      this.setState({ saved: false, status:"error" })
      console.error(error)
    })
  }

  postData(url, data) {
    return fetch(url, {
      body: JSON.stringify(data), // must match 'Content-Type' header
      headers: {
        'content-type': 'application/json'
      },
      method: 'PUT',
    })
    .then(response => response.json()) // parses response to JSON
  }

// Item ******************************

  deleteItem = (item, e) => {
    const items = Object.assign([], this.state.items);
    const index = items.indexOf(item);
    items.splice(index,1);
    this.setState({items:items});
  }

  addItem = (category) => {
    console.log("addItem: ", category);
    console.log(this.state.searchText);
    if (category) {
      const items = Object.assign([], this.state.items);
      const item = { name: this.state.searchText, qty:"", unit:"", tags:category };
      items.push(item);
      this.setState({items:items, showCategoryModal:false, selectedItem:item, openKeypad:true});
    }
  }

  cancelAdd = () => {
    console.log("cancelAdd");
    this.setState({showCategoryModal:false})
  }

  handleAddClick = () => {
    this.setState({showCategoryModal:true});
  }

  handleGridButtonClick = () => {
    this.setState({showCategoryPortal:true});
  }

  closeCategoryPortal = () => {
    this.setState({showCategoryPortal:false});
  }

  handleIndexButtonClick = () => {
    this.setState({showIndexPortal:true});
  }

  closeIndexPortal = () => {
    this.setState({showIndexPortal:false});
  }

  changeItemName = (e) => {
    console.log(e.target.value);
    const items = Object.assign([], this.state.items);
    const index = this.findItemIndexByName(this.state.selectedItem.name);
    const _item = items[index];
    _item.name = e.target.value;
    items[index] = Object.assign({},_item);
    this.setState({items:items, selectedItem:_item});
  }

  handleClick = (item,index,e) => {
    console.log("handleClick: " + item.name);
    // console.log (document.activeElement);
    // console.log(this.searchRef.inputRef);
    if (this.searchRef.inputRef === document.activeElement)
      return;
//      Without setTimeout the modal has not rendered when focus() is called.
//      setTimeouts() are executed last in the function all stack so 0 delay works!
    this.setState({selectedItem:item});//, () => setTimeout(() => this.ref.focus(),0));
    this.openKeypad();
    newInput=true;
  }

  selectItem = (item,e) => {
    console.log("selectItem: " + e.value);
    const index = this.findItemIndexByName(e.value);
//    console.log(index);
    const _item = this.state.items[index];
    this.setState({selectedItem:_item});
  }
 
// Search ************************************

  changeSearchText = (e) => {
//    console.log(e.target.value);
    this.setState({searchText: e.target.value});
  }


  clearSearchText = (e) => {
    e.target.value = "";
    this.searchRef.value = "";
    this.setState({searchText: ""});
  }

  submitSearch = (addable,e) => {
    console.log(addable);
    if (e.key === "Enter") {
      console.log("enter pressed");
      if (addable)
        this.handleAddClick();
    }
  }

  focusSearch = () => {
    // console.log (document.activeElement);
    // console.log(this.searchRef);
    // console.log(this.searchRef === document.activeElement);
    if (!this.state.openKeypad) 
      this.searchRef.focus();
    else
      this.closeKeypad();
  }


  sendClicked = (e) => {
    this.setState({openEmailComposer: !this.state.openEmailComposer});//, () => setTimeout(() => this.ref.focus(),0));
  }

// Keypad *********************

  openKeypad = () => {
    this.setState({openKeypad: true});
  }

  closeKeypad = () => {
    if (this.state.openKeypad) {
      this.setState({openKeypad: false});
    }
  }

  handleMenuClick = () => {
    this.setState({showRelated:false, showCategory:false});
    this.closeKeypad();
  }


// Qty + Unit **************************

  toggleMarked = (item,e) => {
    console.log(e.children);
    const items = Object.assign([], this.state.items);
    const index = this.findItemIndexByName(this.state.selectedItem.name);
    const _item = items[index];
    _item.marked = !_item.marked;
    items[index] = Object.assign({},_item);
    this.setState({items:items, selectedItem:_item});//, openKeypad:true});//, () => setTimeout(() => this.ref.focus(),0));
  }

  setQty = (item,e) => {
//    console.log(e.children);
    const items = Object.assign([], this.state.items);
    const index = this.findItemIndexByName(this.state.selectedItem.name);
    const _item = items[index];
    if (newInput || _item.qty.length > 1) {
      _item.qty="";
    }
    _item.qty = _item.qty + e.children;
    _item.timestamp = Date.now();
    items[index] = Object.assign({},_item);
    this.setState({items:items, selectedItem:_item, changed:true});//, openKeypad:true});//, () => setTimeout(() => this.ref.focus(),0));
    this.searchRef.value="";
    newInput=false;
  }

  clearQty = (e) => {
    const items = Object.assign([], this.state.items);
    const index = this.findItemIndexByName(this.state.selectedItem.name);
    const _item = items[index];
    _item.qty = "";
    _item.unit = "";
    _item.marked = "";
    delete _item.timestamp;
    items[index] = Object.assign({},_item);
    this.setState({items:items, selectedItem:_item});//, openKeypad:true});//, () => setTimeout(() => this.ref.focus(),0));
  }

  setUnit = (item,e) => {
    const newUnit = e.children;
    const items = Object.assign([], this.state.items);
    const index = this.findItemIndexByName(this.state.selectedItem.name);
    const _item = items[index];
    if (_item.unit === newUnit)
      _item.unit = "";
    else
      _item.unit = newUnit;
    _item.timestamp = Date.now();
    items[index] = Object.assign({},_item);
    this.setState({items:items, selectedItem:_item});
//    setTimeout(() => this.setState({openKeypad:false}),600);
  }

  findItemIndexByName = (itemName) => {
    for (let i = 0; i < this.state.items.length; i++) {
      if (this.state.items[i].name === itemName)
        return i;
    }
    return -1;
  }


  statusToColor = () => {
    let ret = "black";
    switch (this.state.status) {
      case "saving" :
        ret = "black";
        break;
      case "saved" :
        ret = "green";
        break;
      case "error" :
        ret = "red";
        break;
      default :
        ret = "black";
    }
    return ret;
  }

// Sort ************************

  handleSortClick = (e) => {
    console.log("handleSortClick: ", e.target.innerHTML);
    let sortMode = e.target.innerHTML;
    const _items = Object.assign([], this.state.items);

    if (sortMode === "Reverse") {
      _items.reverse();
      sortMode = this.state.sortMode;
    } else {
      if (sortMode === 'Alphabetical')
        _items.sort(function(i1,i2) {
          if (i1.name > i2.name)
            return 1
          if (i1.name < i2.name)
            return -1
          return 0;
        });
      if (sortMode === 'Grouped')
        _items.sort(function(i1,i2) {
          if (i1.tags > i2.tags)
            return 1
          if (i1.tags < i2.tags)
            return -1
          else {
            if (i1.name > i2.name)
              return 1
            if (i1.name < i2.name)
              return -1
          }
          return 0;
        });
      if (sortMode === 'None')
        _items.sort(function(i1,i2) {
          let d1 = i1.timestamp?i1.timestamp:0
          let d2 = i2.timestamp?i2.timestamp:0
          console.log('d1:',d1, 'd2:',d2);
          if (d1 > d2)
            return 1
          if (d1 < d2)
            return -1
          return 0;
        });
    }
    console.log(_items);
    this.setState({items:_items, sortMode: sortMode});
  }

// AZ *****************************************  

toggleShowMore = (e) => {
  const _items = Object.assign([], this.state.items);
  const showMore = !this.state.showMore;
  // _items.sort(function(i1,i2) {
  //   if (i1.name > i2.name)
  //     return 1
  //   if (i1.name < i2.name)
  //     return -1
  //   return 0;
  // });
  // if (sortDescending)
  //   _items.reverse();
  // this.setState({items: _items, sortDescending:sortDescending});
    this.setState({showMore:showMore});
  return;    
}

toggleSort = (e) => {
  const _items = Object.assign([], this.state.items);
  const sortDescending = !this.state.sortDescending;
  _items.sort(function(i1,i2) {
    if (i1.name > i2.name)
      return 1
    if (i1.name < i2.name)
      return -1
    return 0;
  });
  if (sortDescending)
    _items.reverse();
  this.setState({items: _items, sortDescending:sortDescending});
  return;    
}

toggleAZ = (e) => {
  this.setState({items: this.state.items.sort(function(i1,i2) {
    if (i1.name > i2.name)
      return 1
    if (i1.name < i2.name)
      return -1
    return 0;
  })})
//    return;    
  if (this.state.selectedLetter.length > 0) {
    this.setState({showIndexPortal: false, selectedLetter:""});
  }
  else {
    if (this.state.showIndexPortal)
      this.setState({showIndexPortal:false});
    else
      this.setState({showIndexPortal:true, openNavigator:false, openKeypad: false});
  }
}

closeAZ = () => {
  console.log("closeAZ");
  this.setState({showIndexPortal: false});
}

selectLetter = (p,e) => {
  console.log(p);
  e.stopPropagation();
  if (this.state.selectedLetter === p) {
    this.deselectLetter();
  }
  else
    this.setState({selectedLetter:p});
}

deselectLetter = () => {
  this.setState({selectedLetter:""});
}


  toggleShowRelated = () => {
    this.setState({showRelated:!this.state.showRelated});
    // remove this if can get toggleShowRelated to wok properly
    // if (this.state.showRelated)
    //   this.closeKeypad();
  }

  toggleShowCategory = (category) => {
    console.log("toggleShowCategory", category);
    let _category = "";
    if (category !== this.state.selectedCategory)
      _category = category;
    this.setState({selectedCategory:_category});
  }

  handleNavClick = (prevItem, e) => {
    console.log(prevItem.name);
    this.setState({selectedItem: prevItem });
    newInput=true;
  }

  // Render ************************
  render() {
    const filteredItems = this.state.items.filter((item, index) => {
      if (this.state.showRelated)
        return (item.tags === this.state.selectedItem.tags)
      if (this.state.selectedCategory.length > 0)
        return (item.tags === this.state.selectedCategory)
      if (this.state.selectedLetter.length > 0)
        return (item.name.charAt(0).toUpperCase() === this.state.selectedLetter.charAt(0).toUpperCase())
      if (this.state.searchText.length === 1) 
        return (item.name.charAt(0).toUpperCase() === this.state.searchText.charAt(0).toUpperCase())
      if (this.state.searchText.length > 1) 
        return (item.name.toUpperCase().includes(this.state.searchText.toUpperCase()))
      return (item.qty || item.unit || item.marked)
    });
    const items = filteredItems.map((item,index) => {
      return (
        <Item
          key={item.name}
          item={item}
          index={index}
          // selectedItem={this.state.selectedItem}
          deleteEvent={this.deleteItem.bind(this, item)}
          rowClickEvent={this.handleClick.bind(this, item, index)}
        > {item.name}</Item>)
    })

    const addable = items.length === 0 && this.state.searchText.length > 3;
    // const currentItemIndex=filteredItems.indexOf(filteredItems.find(item => item.name === this.state.selectedItem.name));
    const currentItemIndex=filteredItems.indexOf(this.state.selectedItem);
    let prevItem, nextItem, prevDisabled, nextDisabled;
    if (currentItemIndex > 0) {
      prevItem = filteredItems[currentItemIndex - 1];
      prevDisabled=false;
    }
    else {
      prevItem = this.state.selectedItem;
      prevDisabled=true;
    }
    if (currentItemIndex < items.length - 1) {
      nextItem = filteredItems[currentItemIndex + 1];
      nextDisabled=false;
    }
    else {
      nextItem = this.state.selectedItem;
      nextDisabled=true;
    }
          
    console.log( JSON.stringify(currentItemIndex));

    return (
      <div>
        <TransitionablePortal basic open={addable} transition={{animation:"slide down", duration: 250}}>
          <Segment inverted className="hidden" textAlign="center" style={{borderRadius:0, position:'fixed', top:56, width:'100%'}}>
            <Button basic inverted onClick={this.handleAddClick}>
              Add <em><b style={{color:'yellow'}}>{this.state.searchText}</b></em>
            </Button>
          </Segment>
        </TransitionablePortal>
        <CategoryPortal items={this.state.items}
          open={this.state.showCategoryPortal}
          onSelect={this.toggleShowCategory}
          onClick={this.closeCategoryPortal}     
          onClose={this.closeCategoryPortal}     
        />
        <IndexPortal items={this.state.items}
          open={this.state.showIndexPortal}
          onClose={this.closeAZ}
          onSelect={this.toggleAZ}
          // onClick={this.closeAZ}    
          items={this.state.items}
          letterSelected={this.selectLetter}      
        />        
        <Categories open={this.state.showCategoryModal}  items={this.state.items} newItemName={this.state.searchText}
            positiveClickHandler={this.addItem}
            negativeClickHandler={this.cancelAdd}
          />
        <EmailComposer items={this.state.items} open={this.state.openEmailComposer} onClose={this.closeEmailComposer.bind(this)}/>
        <Menu size="huge" widths={16} className="ui fixed compact inverted" style={{height:62}} onClick={this.handleMenuClick}>
          <Menu.Item>
            <Input size="huge" ref={searchRef => this.searchRef = searchRef} 
              inverted transparent style={{color:'white',width:'60%'}}            
              onKeyPress={this.submitSearch.bind(this, addable)}
              onChange={this.changeSearchText.bind(this)}
              onFocus={this.clearSearchText}
              onClose={this.clearSearchText}
              value={this.state.searchText}
              type="text"
              // icon=""
              // iconPosition="left" 
            />
          </Menu.Item>
          {/* <Menu.Item position="right" onClick={this.sendData}>
            <Icon link name="send" circular/>
          </Menu.Item> */}
          <Menu.Item position="right"  style={{width:72, position:'fixed', top:0, right:0}}>
            <Popup position="bottom right" header="Sort" 
              trigger={<Icon link name="sort" style={{backgroundColor:this.state.sortMode==='None'?null:"gray"}} circular/>}
              content={
                <Menu secondary vertical>
                  <Divider/>
                  <Menu.Item name='Alphabetical' active={this.state.sortMode === 'Alphabetical'} onClick={this.handleSortClick.bind(this)} />
                  <Menu.Item name='Grouped' active={this.state.sortMode === 'Grouped'} onClick={this.handleSortClick} />
                  <Menu.Item name='None' active={this.state.sortMode === 'None'} onClick={this.handleSortClick} />
                  <Divider/>
                  <Container>
                    <Button floated="right" content='Reverse' onClick={this.handleSortClick} />
                  </Container>
                </Menu>
              }
            >
            </Popup>
          </Menu.Item>
        </Menu>
        {/* <Button size="large" secondary icon="arrow left" style={{position: 'fixed', bottom:'50%', left:4, display:'block', zIndex:20000  }} onClick={this.handlePrevClick.bind(this, prevItem)}/>
        <Button size="large" secondary icon="arrow right" style={{position: 'fixed', bottom:'50%', right:4, display:'block', zIndex:20000  }} onClick={this.handlePrevClick.bind(this, nextItem)}/> */}
        <Button size="huge" secondary circular icon="search" style={{position: 'fixed', top:6, left:6, display:'block', zIndex:700  }} onClick={this.focusSearch}/>
        <Button size="huge" secondary circular icon={<Icon>AZ</Icon>} style={{position: 'fixed', bottom:140, right:36, display:'block', zIndex:700  }} onClick={this.handleIndexButtonClick}/>
        <Button size="huge" secondary circular icon="grid layout" style={{position: 'fixed', bottom:104, right:36, display:'block', zIndex:700  }} onClick={this.handleGridButtonClick}/>
        <Button color={this.statusToColor()} size="massive" circular icon={this.state.changed?"save":"check"}
           style={{boxShadow: "0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22)", position: 'fixed', bottom:32, right:32, display:'block', zIndex:700  }}
           onClick={this.handleSaveSendButtonClick}
        />
        <Table inverted unstackable selectable={false} striped={false} singleLine fixed width={16} style={{marginTop:62, marginBottom:'100%'}}>
          <Table.Body>
            {items}
          </Table.Body>
        </Table>
        {/* <Popup basic inverted  open={this.state.saved} style={{position:'fixed', width:'50%', top:'25%', left:'25%'}}><Container textAlign="center">Saved!</Container></Popup> */}
        <Keypad
          item={this.state.selectedItem}
          itemClicked={this.toggleShowRelated}
          prevDisabled={prevDisabled}
          prevClicked={this.handleNavClick.bind(this, prevItem)}
          nextDisabled={nextDisabled}
          nextClicked={this.handleNavClick.bind(this, nextItem)}
          open={this.state.openKeypad}
          onClose={this.closeKeypad}
          numberClicked={this.setQty}
          unitClicked={this.setUnit}
          deleteClicked={this.clearQty}
          asteriskClicked={this.toggleMarked}
        />
      </div>
    );
  }
}

export default App;



